
import ContactForm from "@/components/ContactForm";

const Therapy = () => {
  return (
    <div>
      <section className="bg-gradient-to-r from-purple-50 to-blue-50 py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <h1 className="text-4xl md:text-5xl font-playfair font-bold text-center mb-6">
            Pediatric Occupational Therapy
          </h1>
        </div>
      </section>

      <section className="py-16">
        <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
          {sections.map((section, index) => (
            <div key={index} className={`flex flex-col ${index % 2 === 0 ? 'md:flex-row' : 'md:flex-row-reverse'} items-center mb-16 gap-8`}>
              <div className="md:w-1/2">
                <img
                  src={section.image}
                  alt={section.title}
                  className="rounded-lg shadow-md w-full h-[300px] object-cover"
                />
              </div>
              <div className="md:w-1/2">
                <h2 className="text-2xl font-playfair font-bold mb-4">{section.title}</h2>
                <p className="text-gray-600">{section.content}</p>
              </div>
            </div>
          ))}
        </div>
      </section>

      <ContactForm />
    </div>
  );
};

const sections = [
  {
    title: "What is Occupational Therapy?",
    content: "Occupational therapy is a type of therapy that helps children improve their ability to perform everyday activities. The goal is to enhance their skills and independence in activities such as self-care, play, school tasks, and social interactions. Occupational therapists work with individuals to overcome challenges and develop the necessary skills for a more fulfilling and independent life.",
    image: "https://picsum.photos/id/237/200/400"
  },
  {
    title: "Professional Expertise",
    content: "Occupational Therapists are highly trained healthcare professionals that evaluate and assist children that are having difficulty participating in meaningful activities or 'occupations' that are needed to conduct their daily routines and live their life to the fullest.",
    image: "https://picsum.photos/id/237/200/400"
  },
  {
    title: "Life Skills Development",
    content: "For a child this may include the development of life skills such as learning at school, playing, completing self-care tasks, relating to others, and fulfilling their role as a family member.",
    image: "https://picsum.photos/id/237/200/400"
  },
  {
    title: "Collaborative Approach",
    content: "Occupational Therapists collaborate with teachers, parents and others to identify and modify barriers that restrict a child's success and model strategies for others to use that will extend the benefits of direct intervention.",
    image: "https://picsum.photos/id/237/200/400"
  },
  {
    title: "Personalized Care",
    content: "Every child is unique, and our approach reflects that. We create individualized treatment plans that address specific challenges while building on each child's strengths. Through engaging activities and evidence-based interventions, we help children develop the skills they need for success.",
    image: "https://picsum.photos/id/237/200/400"
  }
];

export default Therapy;
